/* The Modal (background) */

.loading-modal {
  display: -ms-flexbox;
  display: flex;
  /* this keeps the modal centered when the content (below) is set to margin: auto */
  position: fixed;
  /* Stay in place */
  z-index: 99;
  /* Sit on top */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: #000;
  /* Fallback color */
  background-color: #006eff63;
  /* Black w/ opacity */
}

/* my-modal Content */

.loading-modal-content {
  position: relative;
  background: transparent;
  margin: auto;
  padding: 0;
  -webkit-animation-name: fadein;
          animation-name: fadein;
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
}

.loading-modal img {
  width: 100px;
}

/* Add Animation */

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


